.footer {
  display: flex;
  position: static;
  justify-content: space-between;
}

.footer.hidden, .toggle-all.hidden + label {
  display: none;
}

.filters {
  display: flex;
  position: relative;
}

.filters li {
  display: flex;
  align-items: center;
}

.filters li button {
  color: inherit;
  margin: 3px;
  padding: 3px 7px;
  text-decoration: none;
  border: 1px solid transparent;
  border-radius: 3px;
  cursor: pointer;
}

.filters li button:hover {
  border-color: rgba(175, 47, 47, 0.1);
}

.filters li button.selected {
  border-color: rgba(175, 47, 47, 0.2);
}
